/*eslint-disable*/module.exports={messages:{"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels in Verzug** ist, oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst selbst beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben, und der Mangel danach nicht behoben wurde.":"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels in Verzug** ist, oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst selbst beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben, und der Mangel danach nicht behoben wurde.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.":"- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.":"- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.","- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch und die Wohnqualität stark beeinträchtigt ist.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in, und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren können. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.":"- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch und die Wohnqualität stark beeinträchtigt ist.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in, und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren können. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag festgelegt wurde.","- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.":"- Требование **Возврат платы за билет** — это право на возврат оплаченной стоимости билета в течение одной недели в случае отмены.\n- Кроме того, вы получите обратный рейс до первого места вылета, если стыковочный рейс был отменен.\n- Вы можете выбрать трансфер авиакомпании в пункт назначения вместо возмещения расходов.","- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln**, oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist, oder diesen bei Vertragsabschluss verschwiegen hat.":"- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln**, oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist, oder diesen bei Vertragsabschluss verschwiegen hat.","Aufwendungsersatz":"возмещение расходов","Ausgleichszahlung":"возвратный платеж","Außergerichtliche Einigung":"Внесудебное урегулирование","Beratungsangebote":"Beratungsangebote","Beseitigung des Mangels durch die Vermieter:in":"Устранение дефекта арендодателями","Betreuungsleistungen":"услуги по уходу","Die Justiz im Netz.":"Судебная власть в сети.","Diese Option ist leider noch nicht verfügbar.":"К сожалению, эта опция пока недоступна.","Dokumentation des Mangels":"Документирование дефекта","Einfach, online, jederzeit.":"Легко, онлайн, в любое время.","Erstberatung":"первичная консультация","Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>":"Если вы не уверены, вы также можете обратиться за советом. Для этого мы <0>кратко изложили консультационные услуги здесь.</0>","Fluglinie kontaktieren":"Свяжитесь с авиакомпанией","Gerichtsverfahren":"разбирательств","Handlungsoptionen vergleichen":"Сравните варианты действий","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"В какой сфере вашей жизни у вас есть проблемы?","Info":"Информация","Ja":"да","Jetzt starten!":"Начать сейчас","Lösungsfinder":"Поиск решений","Mehr erfahren":"Подробнее","Mietminderung":"снижение арендной платы","Mängelanzeige bei Ihrer Vermieter:in":"Уведомление о дефектах вашему арендодате:в","Mögliche Ansprüche":"Возможные претензии","Mögliche Lösungen":"Возможные решения","Nein":"нет","Nur kostenlose Angebote anzeigen":"Показать только бесплатные предложения","Nächste Schritte":"Следующие шаги","Online Klage einreichen":"Послать иск в Интернете","Optionen":"Параметры","Problem einordnen":"Классифицировать проблему","Rechtlich beraten lassen":"Получите юридическую консультацию","Rechtsproblem einordnen":"Классифицировать юридические проблемы","Rückerstattung der Flugscheinkosten":"Возмещение стоимости билетов","Schadensersatz":"убытков","Schlichtungsantrag stellen":"Подать запрос на примирение","Schritt":"Шаг","Startseite":"Главная страница","Unser Angebot":"Наше предложение","Vermieter:in erneut kontaktieren":"Арендодатель снова свяжитесь","Wegweiser":"Wegweiser","Weitere Möglichkeiten":"Weitere Möglichkeiten","Wie es funktioniert":"Как это работает","Wobei können wir Ihnen helfen?":"Чем мы можем вам помочь?","ZPO Informationen":"Информация о ZPO","Zum Prototypen":"Прототип","Zurück":"назад","Zurück zur Homepage":"Вернуться на главную страницу","back":"назад","consultation.header":"консультационные услуги","consultation.sub_header":"Местные и межрегиональные консультационные центры могут помочь вам лично и индивидуально в вашем случае. Здесь мы перечислили различные предложения. В чем вам нужна поддержка?","exit.footer":"Вам нужна дополнительная поддержка? Затем вы можете обратиться в юридические консультационные центры, которые проинформируют вас о вашем деле индивидуально. В ходе консультации эксперты оценивают, существуют ли судебные иски и как наилучшим образом обеспечить их исполнение. Для этого мы [предоставили обзор консультационных центров здесь] (/консультационные услуги).","exit.heading":"К сожалению, мы не покрыли ваше дело.","forward":"Далее","home.hero_offer":"JustizPortal.de — это предложение Федерального министерства юстиции и защиты прав потребителей в сотрудничестве с федеральными министерствами юстиции федеральных земель. Портал предоставляет обзор ваших прав в различных повседневных ситуациях. Это также помогает классифицировать и решать юридические вопросы и проблемы. Вы будете проинформированы о возможных вариантах действий и получите поддержку в отстаивании ваших прав.","home.howitwork_claims":"Затем мы поможем вам оценить, какие претензии могут существовать в вашем случае. Претензии — это право требовать от других конкретных действий (например, возмещение ущерба), потерпеть (например, сдавать квартиру в субаренду) или воздерживаться от (например, въезда в собственность). invidunt it labore.","home.howitwork_classify":"Во-первых, мы поможем вам выяснить, есть ли юридическая проблема в вашем случае. Мы предоставим вам информацию, адаптированную к вашему случаю.","home.howitwork_heading":"Как это работает","home.howitwork_lawsuit":"Когда все остальные варианты исчерпаны, мы поможем вам подать иск в районный суд онлайн. С этой целью мы объясняем вам процесс нашего онлайн-процесса и предоставляем прозрачную информацию о стоимости судебных разбирательств, а также возможные юридические консультации.","home.howitwork_options":"Исходя из этого, мы предложим возможные решения для <0>вас - первоначально для дружеского разрешения споров с противоположной стороной.</0>","home.howitwork_subheading":"Мы поможем вам решить вашу юридическую проблему в четыре простых шага:","home.howtohelp_footer":"Вы можете узнать больше о юридических проблемах, с которыми мы можем вам помочь, на следующих страницах.","home.sub_heading":"Мы информируем вас о ваших правах в повседневной жизни и помогаем решить юридическую проблему в простых шагах","nextsteps.header":"Ваши следующие возможные шаги к решению проблем","nextsteps.sub_header":"Здесь мы обобщили следующие шаги по обеспечению соблюдения ваших требований. Чтобы узнать больше, поочередно<0/> нажимайте на соответствующие флажки.","pe.header":"Возможные судебные иски","pe.sub_header":"Из вашей информации могут возникнуть следующие претензии. Претензия — это право требовать что-то от другого лица или компании. Кроме того, можно применить несколько требований одновременно. \nНажмите на претензии, чтобы узнать больше о них. Затем мы сообщим вам о возможных дальнейших шагах на следующей странице.","pe.to_next_steps":"К следующим шагам","zpo.nextsteps":"1. На следующей странице вы можете создать документ для судебного процесса, связанного с вашим делом. \n2. Мы оценим для вас судебные издержки и проинформируем вас о следующих шагах, которые вам необходимо предпринять. \n3. После создания вы можете сначала просмотреть всю информацию. **Только после этого вы принимаете решение о подаче заявки. **","Über Ansprüche informieren":"Сообщите о претензиях"}};